html {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

body {
  background-color: #1a1b15;
  font-family: "Poppins", sans-serif;
}

h2 {
  font-size: 2rem;
  text-align: center;
  color: #ffffff;
}

h3 {
  text-align: center;
  color: #ffffff;
}

h4 {
  font-size: 1.5rem;
  color: #ffffff;
}

.animated {
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}

.fast {
  -webkit-animation-duration: 0.4s;
  animation-duration: 0.4s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.fadeIn {
  animation-name: fadeIn;
}

nav {
  z-index: 200;
  position: fixed;
  width: 100%;
  height: 3.5rem;
  background-color: #1d294a;
}
nav .header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-family: "Poppins", sans-serif;
  font-weight: 700;
}
nav .header .logo a {
  margin-left: 1rem;
  color: #ffffff;
  font-size: 3rem;
  text-decoration: none;
}
@media (max-width: 480px) {
  nav .header .logo {
    display: none;
  }
}
nav .header .menu a {
  margin-right: 1rem;
  color: #ffffff;
  font-size: 2rem;
  text-decoration: none;
  transition: all 0.4s ease-out;
}
nav .header .menu a:hover {
  color: #afafaf;
  border-bottom: solid 1px #ffffff;
}
@media (max-width: 480px) {
  nav .header .menu a {
    font-size: 1.5rem;
    margin-right: 0;
  }
}
@media (max-width: 480px) {
  nav .header .menu {
    margin-top: 1rem;
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 100%;
  }
}

.inicio {
  background-image: url("../../../build/img/2.webp");
  background-position: center center;
  background-size: cover;
  height: 100vh;
  display: flex;
  justify-content: center;
}
.inicio .titulo-oculto {
  display: none;
}
@media (max-width: 800px) {
  .inicio .titulo-oculto {
    margin-top: 50%;
    color: #ffffff;
    font-size: 6rem;
    display: block;
    position: absolute;
    z-index: 100;
  }
}
.inicio .about {
  position: absolute;
  top: 58%;
  mix-blend-mode: normal;
  box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.5);
  background-color: #1d294a;
  width: auto;
  height: auto;
  border-radius: 10px;
}
.inicio .about p {
  margin: 10px auto;
  width: 90%;
  color: #ffffff;
  text-align: center;
}
@media (max-width: 800px) {
  .inicio .about {
    height: auto;
    bottom: 20%;
    width: auto;
    border-radius: 0;
  }
  .inicio .about p {
    margin: 0 auto 0;
    width: 98%;
    font-size: 0.8rem;
  }
}
.inicio a {
  display: flex;
  font-size: 1rem;
  align-items: center;
  justify-content: center;
  color: #ffffff;
  width: 15%;
  height: 7%;
  top: 85%;
  text-decoration: none;
  position: absolute;
  box-shadow: 8px 7px 5px 3px rgba(0, 0, 0, 0.74);
  -webkit-box-shadow: 8px 7px 5px 3px rgba(0, 0, 0, 0.74);
  -moz-box-shadow: 8px 7px 5px 3px rgba(0, 0, 0, 0.74);
  border-radius: 50px;
  background-color: #3a6984;
  transition: all 0.4s ease-out;
}
@media (max-width: 800px) {
  .inicio a {
    display: none;
  }
}
.inicio a:hover {
  transform: scale(1.2);
  box-shadow: 18px 17px 24px 3px rgba(0, 0, 0, 0.74);
  -webkit-box-shadow: 18px 17px 24px 3px rgba(0, 0, 0, 0.74);
  -moz-box-shadow: 18px 17px 24px 3px rgba(0, 0, 0, 0.74);
}

.productos {
  display: flex;
  justify-content: center;
}
.productos .contenedor-productos {
  margin-top: 40px;
  background-color: #1d294a;
  width: 98%;
  border-radius: 10px;
}
.productos .contenedor-productos .cards {
  margin: 0 auto;
}
.productos .contenedor-productos .cards .contenedor-cards {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
}
.productos .contenedor-productos .cards .contenedor-cards .product {
  background-color: #3a6984;
  border-radius: 50px;
  text-align: center;
  position: relative;
  width: 380px;
  height: 380px;
  color: white;
  cursor: pointer;
  transition: 1s ease-in-out;
  transform-style: preserve-3d;
  box-shadow: 0px 10px 20px #000000;
  margin-bottom: 30px;
}
.productos .contenedor-productos .cards .contenedor-cards .product:hover {
  transform: rotateY(0.5turn);
}
.productos .contenedor-productos .cards .contenedor-cards .product .card__face {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  font-size: 1.3rem;
  backface-visibility: hidden;
  transition: 1s ease-in-out;
}
.productos .contenedor-productos .cards .contenedor-cards .product .card__face img {
  margin-top: 20px;
  border-radius: 50%;
  width: 70%;
  height: 70%;
}
.productos .contenedor-productos .cards .contenedor-cards .product .card__face--front {
  z-index: 200;
}
.productos .contenedor-productos .cards .contenedor-cards .product .card__face--front p {
  font-size: 3rem;
  position: relative;
  margin-top: 0;
  width: 100%;
}
.productos .contenedor-productos .cards .contenedor-cards .product .card__face--back {
  display: flex;
  flex-direction: column;
  align-items: center;
  transform: rotateY(0.5turn);
}
.productos .contenedor-productos .cards .contenedor-cards .product .card__face--back table,
.productos .contenedor-productos .cards .contenedor-cards .product .card__face--back tr {
  border: 1px solid #ffffff;
  text-align: left;
  font-size: 80%;
}
.productos .contenedor-productos .cards .contenedor-cards .product .card__face .boton-ver {
  width: 50%;
}
.productos .contenedor-productos a {
  cursor: pointer;
  margin: 10px auto;
  text-align: center;
  text-decoration: none;
  background: rgba(4, 4, 4, 0.81);
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.5);
  border-radius: 50px;
  display: flex;
  font-size: 1rem;
  align-items: center;
  justify-content: center;
  color: #ffffff;
  height: 40px;
  transition: all 0.4s ease-out;
}
@media (max-width: 800px) {
  .productos .contenedor-productos a {
    width: 25%;
  }
}
.productos .contenedor-productos a:hover {
  transform: scale(1.2);
  box-shadow: 18px 17px 24px 3px rgba(0, 0, 0, 0.74);
}
.productos .imagen-producto {
  position: fixed;
  background-color: rgba(0, 0, 0, 0.89);
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 200;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-around;
  align-items: center;
  align-content: stretch;
}
@media (max-width: 800px) {
  .productos .imagen-producto {
    flex-direction: column;
  }
}
.productos .imagen-producto .imagen-zoom {
  width: 50%;
  height: 80%;
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  align-content: stretch;
}
@media (max-width: 800px) {
  .productos .imagen-producto .imagen-zoom {
    height: 50%;
    width: 100%;
  }
}
.productos .imagen-producto .imagen-zoom img {
  border-radius: 15px;
  width: 90%;
}
@media (max-width: 800px) {
  .productos .imagen-producto .imagen-zoom img {
    width: 90%;
  }
}
.productos .imagen-producto .datos-zoom {
  width: 40%;
}
@media (max-width: 800px) {
  .productos .imagen-producto .datos-zoom {
    height: 50%;
  }
}
.productos .imagen-producto .datos-zoom .tabla {
  width: 100%;
  color: white;
}
.productos .imagen-producto .datos-zoom .boton-cerrar {
  width: 35%;
}

/** page structure **/
#wrapper {
  border-radius: 15px;
  background: rgba(255, 255, 255, 0.897);
  margin: 0 auto;
  padding: 10px 17px;
}
@media (max-width: 800px) {
  #wrapper {
    height: auto;
    width: 80%;
    margin-bottom: auto;
  }
}

#keywords {
  margin: 0 auto;
  font-size: 1.2em;
  margin-bottom: 15px;
  width: 80%;
}
@media (max-width: 800px) {
  #keywords {
    width: 100%;
    font-size: 0.7em;
  }
}

#keywords-1 {
  width: 80%;
  font-size: 1em;
  margin-bottom: 15px;
}

#keywords thead {
  background: #80b4db;
}

#keywords-1 thead {
  background: #80b4db;
}

#keywords thead tr th {
  font-weight: bold;
  padding: 12px 30px;
  padding-left: 42px;
}

#keywords-1 thead tr th {
  font-weight: bold;
  text-align: center;
  padding: 5px 15px;
}

#keywords thead tr th span {
  padding-right: 20px;
  background-repeat: no-repeat;
  background-position: 100% 100%;
}

#keywords thead tr th.headerSortUp,
#keywords thead tr th.headerSortDown {
  background: #acc8dd;
}

#keywords tbody tr {
  color: #555;
}

#keywords tbody tr td {
  text-align: center;
  padding: 15px 10px;
}
@media (max-width: 800px) {
  #keywords tbody tr td {
    padding: 0px 5px;
  }
}

#keywords-1 tbody tr td {
  text-align: center;
}

#keywords tbody tr td.lalign {
  text-align: left;
}

.contacto {
  margin-top: 20px;
  display: flex;
  justify-content: center;
}
.contacto .contenedor-contacto {
  display: flex;
  flex-direction: column;
  background-color: #3a6984;
  width: 98%;
  border-radius: 10px;
}
.contacto .contenedor-contacto .datos-mapa {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
@media (max-width: 800px) {
  .contacto .contenedor-contacto .datos-mapa {
    flex-direction: column;
    text-align: center;
  }
}
.contacto .contenedor-contacto .datos-mapa .datos-contacto {
  padding-left: 10%;
}
@media (max-width: 800px) {
  .contacto .contenedor-contacto .datos-mapa .datos-contacto {
    padding-left: 0px;
  }
}
.contacto .contenedor-contacto .datos-mapa .datos-contacto p {
